import Image from "next/image";
import { useState } from "react";

export default function Home() {


    type Product = {
      count: number;
      theme: string;
    }


  const [state, setState] = useState<Product>({count: 0, theme: 'dark'})


  function counter() {
    setState((prev)=>{
      return {
        ...prev,
        count: prev.count + 1,
        theme: "dark"
      }
    })
  }


  return (
    <div className="flex min-h-screen items-center justify-center bg-zinc-50 font-sans dark:bg-black">
      <h2>{state.count}</h2>
      <h2>{state.theme}</h2>
      <button onClick={counter} />
    </div>
  );
}
